  
import React, { Component } from 'react'

export default class AboutMe extends Component {
  render() {
      return ( 
        <div> 
        <section className="page-section" id="services">
          <div className="container">
            <div className="text-center">
              <h2 className="section-heading text-uppercase">About me</h2>
              <h3 className="section-subheading text-muted">Learning and leveling up one day at a time</h3>
            </div>
            <div className="row text-center">
              <div className="col-md-4">
                <span className="fa-stack fa-4x">
                  <i className="fas fa-circle fa-stack-2x text-primary" />
                  <i className="fas fa-user fa-stack-1x fa-inverse" />
                </span>
                <h4 className="my-3">Myself</h4>
                <p className="text-muted">Hi! I'm Guzmán Senosiaín, an enthusiastic Uruguayan <b>Software Developer</b> with +4 years of experience working on-site and remote on successfull <b>web/mobile projects</b>. I'm a Software Engineer graduated from ORT University in 2014. Languages: <b>Spanish, English and Portuguese.</b></p>
              </div>
              <div className="col-md-4">
                <span className="fa-stack fa-4x">
                  <i className="fas fa-circle fa-stack-2x text-primary" />
                  <i className="fas fa-laptop fa-stack-1x fa-inverse" />
                </span>
                <h4 className="my-3">Experience</h4>
                <p className="text-muted">Since 2015, I have participated in different projects using technologies such as: <b>Java, Javascript, HTML5, CSS, Maven, SAPUI5, Bootstrap, Sencha, Spring, Eclipse, Postman.</b> </p>
              </div>
              <div className="col-md-4">
                <span className="fa-stack fa-4x">
                  <i className="fas fa-circle fa-stack-2x text-primary" />
                  <i className="fas fa-globe-americas fa-stack-1x fa-inverse" />
                </span>
                <h4 className="my-3">International</h4>
                <p className="text-muted">In 2017, I had an amazing experience while I was living in Edmonton (Canada) and working on the mobile app of ATB Bank. Then, during 2019 I was located and studying
                  in Barcelona (Spain), and achieved extra certifications in HTML5, CSS3, Bootstrap, Java and Javascsript.</p>
              </div>
            </div>
          </div>
        </section>
        </div>
      );
    }
  }