import React, { Component } from 'react'

export default class Contact extends Component {
  render() { 
      return (
  
        <section className="page-section bg-light" id="team">
          <div className="container-fluid">
            <div className="text-center">
              <h2 className="section-heading text-uppercase">Contact Me</h2>                  
            </div>
            <div className="row">
              <div className="col-lg-12">
                <div className="team-member">
                  <img className="mx-auto rounded-circle" src="assets/img/profilePhoto.jpg" alt="" />
                  <h4>Guzmán Senosiaín</h4>
                  <p><span className="badge badge-danger">E-mail</span><strong>  g.senosiain91@gmail.com</strong></p>
                  <a className="btn btn-info btn-social mx-2" href="https://twitter.com/guzmansb7"><i className="fab fa-twitter" /></a>
                  <a className="btn btn-dark btn-social mx-2" href="https://www.facebook.com/guzman.senosiain"><i className="fab fa-facebook-f" /></a>
                  <a className="btn btn-warning btn-social mx-2" href="https://www.linkedin.com/in/guzmansenosiain/"><i className="fab fa-linkedin-in" /></a>
                </div>
              </div>         
            </div>
            <div className="row">
              <div className="col-lg-8 mx-auto text-center"><p className="large text-muted">I'm currently living in Montevideo (Uruguay) and open to challenging on-site or remote (freelance) dev projects. Please feel free to contact me at my personal e-mail or any social network. Thanks!</p></div>
            </div>
          </div>
        </section>
      );
    }
  }

